.fade{
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    right: 0;
    background-color: #000;
    opacity: .3;
}

.modal{
    position: absolute;
    top: 10%;
    left: 0;
    right: 0;
    margin: 0 auto;
    width: 500px;
    height: 500px;
    z-index: 1;
    background-color: #fff;
    display: flex;
    flex-direction: column;
    justify-content: center;
    text-align: center;
}

.modal h2{
    margin-bottom: 1em;
}

.modal form{
    width: 90%;
}

@media only screen and (max-width: 550px){
    .modal{
        width: 80%;
    }
}